/*App.js (main page)*/
body{
    background-color: rgba(244, 245, 238);
}

.mainButtonsTemplate{
    margin: 15% auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    height: 100%;
    background-color: rgba(218, 219, 210, 0.9);
    box-shadow: 0px 0px 45px 50px rgba(218, 219, 210, 0.9)
}

.chooseButtons {
    width: 200px;
    height: 200px;
    border: 5px solid rgba(128, 128, 128, 0.2);
    border-radius: 5%;
    transition: all 0.1s;
    font-family: "Aboreto", cursive;
    font-weight: bold;
    font-size: 25px;
    text-shadow: 3px 3px 3px gray;;
    box-shadow: 5px 5px 20px gray;
}

.chooseButtons:hover {
    color: white;
    width: 250px;
    height: 250px;
    transform-origin: center;
    background-color: black;
    font-size: 30px;
}

.add:hover{
    background-color: rgba(183, 200, 180, 0.7);
}

.training:hover {
    background-color: rgba(132, 236, 113, 0.7);
}

.vocabluary:hover {
    background-color: rgba(212, 222, 148, 0.7);
}

.chooseButtons:active {
    transition: left 5px;
    width: 190px;
    height: 190px;
}

.hideElement {
    display: none;
}
/*App.js (main page end)*/